import { h, Component } from 'preact'
import style from './style.css'

class Button extends Component {
  render(props) {
    const { href, target, title } = props
    return h('a', { href, target, class: style.link }, [
      h('div', { class: style.button }, title),
    ])
  }
}

class DownloadResumeButton extends Button {
  render() {
    const attributes = {
      title: 'Donwload Resume',
      href: 'assets/documents/resume.pdf',
      target: '_blank',
    }
    return super.render(attributes)
  }
}

class ActionButtons extends Component {
  render() {
    return h('div', { class: style.actionButtons }, [
      h(DownloadResumeButton),
    ])
  }
}

export default ActionButtons



