import { h, Component } from 'preact'
import style from './style.css'

class Card extends Component {
  render(props) {
    const { class: className, children } = props
    const cardClass = className ? `${className} ${style.card}` : style.card
    return h('div', { ...props, class: cardClass }, children)
  }
}

export class CardHeader extends Component {
  render(props) {
    const { class: className, children } = props
    const cardHeaderClass = className ? `${className} ${style.cardHeader}` : style.cardHeader
    return h('div', { ...props, class: cardHeaderClass }, children)
  }
}

export default Card



