import { h, Component } from 'preact'
import style from './style.css'

class SplitRow extends Component {
  render(props) {
    const { children: [left, right] } = props
    let { class: rowClass } = props
    let { attributes: { class: leftClass } } = left
    let { attributes: { class: rightClass } } = right

    rowClass = rowClass ? `${rowClass} ${style.row}` : style.row
    leftClass = leftClass ? `${leftClass} ${style.left}` : style.left
    rightClass = rightClass ? `${rightClass} ${style.right}` : style.right

    return h('div', { ...props, class: rowClass }, [
      h(left.nodeName, { ...left.attributes, class: leftClass }, left.children),
      h(right.nodeName, { ...right.attributes, class: rightClass }, right.children),
    ])
  }
}

export default SplitRow



