import { h, Component } from 'preact'
import Card from '../card'
import Row from '../row'
import education from './education'
import style from './style.css'

class Degree extends Component {
  render(props) {
    const { degree, major, graduationYear } = props
    return h(Row, { class: style.degree }, [
      h('div', { class: style.title }, [degree, ' in ', major]),
      h('div', { class: style.graduationYear }, graduationYear),
    ])
  }
}

class Institution extends Component {
  render(props) {
    const { institution, location } = props
    return h(Row, { class: style.institutionInfo }, [
      h('div', { class: style.institution }, institution),
      h('div', { class: style.location }, location),
    ])
  }
}

class Diploma extends Component {
  render(props) {
    return h(Card, { class: style.diploma }, [
      h(Degree, props),
      h(Institution, props),
    ])
  }
}

class Education extends Component {
  render() {
    return h('div', { class: style.education }, [
      h('h2', {}, 'Education'),
      ...education.map(degree => h(Diploma, degree)),
    ])
  }
}

export default Education



