import { h, Component } from 'preact'
import Card, { CardHeader } from '../card'
import Row from '../row'
import publications from './publications'
import style from './style.css'

class Paper extends Component {
  render(props) {
    const { title, completionYear, assetID } = props
    const assetLink = this.getAssetLink(assetID)
    const linkAttributes = { href: assetLink, class: style.link, target: '_blank' }

    return h(Row, { class: style.paper }, [
      h('a', linkAttributes, h('div', { class: style.title }, title)),
      h('div', { class: style.completionYear }, completionYear),
    ])
  }

  getAssetLink(assetID) {
    return `assets/documents/${assetID}.pdf`
  }
}

class Publication extends Component {
  render(props) {
    const { abstract } = props
    return h(Card, { class: style.publication }, [
      h(CardHeader, {}, h(Paper, props)),
      h('div', { class: style.abstract }, abstract),
    ])
  }
}

class Publications extends Component {
  render() {
    return h('div', { class: style.publications }, [
      h('h2', {}, 'Publications'),
      ...publications.map(publication => h(Publication, publication)),
    ])
  }
}

export default Publications



