import { h, Component } from 'preact'
import Card, { CardHeader } from '../card'
import Row from '../row'
import experiences from './experiences'
import style from './style.css'

const shouldAggregatePositions = true

class Positions extends Component {
  renderPosition(position) {
    const { title, start, end } = position
    const tenure = start === end ? start : [start, ' - ', end]
    return h(Row, { class: style.position }, [
      h('div', { class: style.title }, title),
      h('div', { class: style.tenure }, tenure),
    ])
  }

  render(props) {
    let { positions } = props
    if (shouldAggregatePositions) positions = this.aggregatePositions(positions)
    return h('div', { class: style.positions }, [
      ...positions.map(position => this.renderPosition(position)),
    ])
  }

  aggregatePositions(positions = []) {
    const aggregatedPosition = positions.reduce((memo, next) => {
      const { position, start, end } = next
      if (end > memo.end) return { ...memo, position, end }
      if (start < memo.start) return { ...memo, start }
      return memo
    })
    return [aggregatedPosition]
  }
}

class Duties extends Component {
  render(props) {
    const { duties } = props
    return h('ul', { class: style.duties }, [
      ...duties.map(duty => h('li', { class: style.duty }, duty)),
    ])
  }
}

class Technologies extends Component {
  render(props) {
    const { technologies } = props
    return h('div', { class: style.technologies }, [
      h('span', {}, 'Stacks: '),
      h('span', { class: style.stacks }, technologies.join(', ')),
    ])
  }
}

class Employer extends Component {
  render(props) {
    const { employer, location } = props
    return h(Row, { class: style.employerInfo }, [
      h('div', { class: style.employer }, employer),
      h('div', { class: style.location }, location),
    ])
  }
}

class Experience extends Component {
  render(props) {
    return h(Card, { class: style.experience }, [
      h(CardHeader, {}, [h(Positions, props), h(Employer, props)]),
      h(Technologies, props),
      h(Duties, props),
    ])
  }
}

class Experiences extends Component {
  render() {
    return h('div', { class: style.experiences }, [
      h('h2', {}, 'Experience'),
      ...experiences.map(experience => h(Experience, experience)),
    ])
  }
}

export default Experiences



